<template>
   <div class="ContainerMovie">
      <Home  
         v-if="getLabelMovie == 'Newest Movies'" 
         :getLoader="getLoader"
      />
      <Search 
         v-if="getLabelMovie == 'Search Result'" 
         :getLoader="getLoader"
      />
   </div>
  
</template>


<script>
   export default {
      components:{
         Home : () => import ("./Home/Home"),
         Search : () => import ("./Search/Search")
      },
      props:[
         //This props from parent Content.vue
         'getLoader',
         'getLabelMovie'
      ],
      mounted() {
         // every reload page methods LoadList will be triggerd
         this.LoadList()
      },
      methods: {
         LoadList(){
            const varPage = 1
            this.$store.dispatch('GET_FIRSTDATA', varPage);
            // This to trigger loader if state same 'true'
            this.$store.commit('MutLoaderDt', true);
         }
      },
      watch: {
         getLabelMovie(val){
            // This function for monitor props 'getLabelMovie' 
            //or state LabelMovie if there any change  will be showing or hide page search or home pages
            if (val === 'Newest Movies'){
               this.$store.commit('MutValSearch', null)
            }
         }
      },
      
   }
</script>

<style lang="scss">
	@import './Main.scss';
</style>



